import React from 'react';
import 'react-day-picker/lib/style.css';

function View({
                  nextMonth,
                  previousMonth,
                  onPreviousClick,
                  onNextClick,
                  className,
                  localeUtils,
                }) {
  const months = localeUtils.getMonths();
  const prev = months[previousMonth.getMonth()];
  const next = months[nextMonth.getMonth()];
  const styleLeft = {
    float: 'left',
    width: '15px',
  };
  const styleRight = {
    float: 'right',
    width: '15px',
  };
  return (
    <div className={className}>
      <a href='#' style={styleLeft} onClick={() => onPreviousClick()}>
        <img src="data:image/png;base64," alt="left"/>
      </a>
      <a href='#' style={styleRight} onClick={() => onNextClick()}>
        <img src="data:image/png;base64," alt="right"/>
      </a>
    </div>
  );
}

export {View}
